<script>
import foot from './public/foot.vue'
import topMember from './public/topMember.vue'
export default {
  components: {
    foot,
    topMember
  },
  data() {
    return {
      checkDetail:false,
      params:{
        state:'',
        dateValue:'',
        page:0,
        size:10
      },
      total:0,
       page2:1,
      size2:10,
      total2:0,
      input1: '',
      select:'',
      activeName: 'first',
      addForm:{
        packNum:'',
        reason:'',
        remark:''
      },
      tableData: [],
        tableData2: [],
        lookdetail:false
    };
  },
  mounted(){
    this.search();
     this.search2();
  },
  methods:{
     search(){
      let that = this;

      
      var paramsData = that.params
      for(var key in paramsData){
        if(paramsData[key] === ''){
          delete paramsData[key]
        }
      }
      const {state,dateValue,page,size} = paramsData
      if(dateValue){
        var begin = dateValue[0]
        var end = dateValue[1]
      }
      this.axios
        .get(
          // process.env.API_HOST+'queryPack',
          '/giftmall/queryPack',
          {
            params:{
              begin,
              end,
              state,
              page,
              size
            },
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              "domain": this.url,
              "Authorization":sessionStorage.getItem('token')
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
             for(var i=0;i<response.data.data.list.length;i++){
                response.data.data.list[i].createTime=that.gettime(response.data.data.list[i].createTime);
                if(response.data.data.list[i].state===1){
                  response.data.data.list[i].state2 = '已处理' 
                }else if(response.data.data.list[i].state===0){
                  response.data.data.list[i].state2 = '未处理' 
                }else if(response.data.data.list[i].state===-1){
                  response.data.data.list[i].state2 = '已驳回' 
                }
             }
             that.tableData=response.data.data.list;
             console.log(that.tableData)
             that.total=response.data.data.totalCount;
          } 
        });
    },
    search2(){
      let that = this;
      const {page,size} =  this.params
      this.axios
        .get(
          // process.env.API_HOST+' queryPack/bufa',
          '/giftmall/queryPack/bufa',
          {
            params:{
              page,
              size,

            },
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              "domain": this.url,
              "Authorization":sessionStorage.getItem('token')
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
             for(var i=0;i<response.data.data.list.length;i++){
                response.data.data.list[i].createTime=that.gettime(response.data.data.list[i].createTime);
             }
             that.tableData2=response.data.data.list;
             that.total2=response.data.data.totalCount;
          } 
        });
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.size=val;
      this.search();
    },
    handleCurrentChange(val) {
      this.params.page = val - 1
      console.log(`当前页: ${val}`);
      this.page=val;
      this.search();
    },
    handleSizeChange2(val) {
      console.log(`每页 ${val} 条`);
      this.size2=val;
      this.search2();
    },
    handleCurrentChange2(val) {
      console.log(`当前页: ${val}`);
      this.page2=val;
      this.search2();
    },
    gettime(t){
        var _time=new Date(t);
        var   year=_time.getFullYear();//2017
        var   month=_time.getMonth()>8?(_time.getMonth()+1):'0'+(_time.getMonth()+1);//7
        var   date=_time.getDate()>9?_time.getDate():'0'+_time.getDate();//10
        var   hour=_time.getHours()>9?_time.getHours():'0'+_time.getHours();;//10
        var   minute=_time.getMinutes()>9?_time.getMinutes():'0'+_time.getMinutes();;//56
        var   second=_time.getSeconds()>9?_time.getSeconds():'0'+_time.getSeconds();;//15
        
        return   year+"-"+month+"-"+date+"   "+hour+":"+minute+":"+second;//这里自己按自己需要的格式拼接
    },
    addCheck(){
        let that=this;
        // this.axios.post(process.env.API_HOST+'queryPack',
        this.axios.post('/giftmall/queryPack',
        this.qs.stringify(this.addForm),{
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
                // 'domain':'taobao1.tianyalei.com',
                'domain':this.url,
                "Authorization":sessionStorage.getItem('token')
              }
            })
        .then(function (response) {
            if(response.data.code==200){
                that.$message({
                  message: '添加成功！',
                  type: 'success'
                });
                that.addForm.packNum='';
                that.addForm.reason='';
                that.addForm.remark=''
            }else{
                that.$message({
                  message: response.data.message,
                  type: 'warning'
                });
            }
          })
    },
      look(){
        this.lookdetail=true;
      }
  },
  computed:{
    
    
  }
};
</script>

